/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

.main {
  display: flex;
  flex-direction: column; }
  @media all and (min-width: 768px) {
    .main {
      flex-direction: row; } }

body {
  font-family: "Urbanist", sans-serif; }

.favourite {
  background-color: #EE6F57;
  color: #F6F5F5; }

.estandar {
  background-color: #F6F5F5;
  color: #145374; }

.header {
  background-color: #00334E; }
  .header__title {
    font-family: "Tourney", cursive;
    font-size: 2rem;
    color: #F6F5F5; }

.fav {
  display: flex;
  flex-direction: column; }
  .fav__menu {
    display: flex;
    justify-content: space-around; }
  .fav__title {
    font-size: 20px;
    color: #00334E; }
  .fav__reset {
    width: 30px;
    height: 30px;
    border-radius: 5px;
    background-color: #145374;
    color: #F6F5F5; }

.fav-list {
  display: flex;
  flex-direction: column; }
  .fav-list__card {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 140px; }
  .fav-list__image {
    width: 60px;
    border: 2px solid #EE6F57; }

.closed {
  display: none; }

.found-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  border-radius: 10px; }
  .found-list__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    border: 2px solid #00334E;
    margin: 10px 0;
    padding: 10px;
    width: 200px;
    height: 300px; }
  .found-list__image {
    width: 150px; }
  .found-list__title {
    font-size: 18px; }
  @media all and (min-width: 768px) {
    .found-list {
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap: wrap;
      column-gap: 20px; } }
